.scroll-progress__container {
    display: block;
    width: 100%;
    height: 5px;
    position: fixed;
    top: 0;
    left: 0;
    background-color: #ddd;
}

.scroll-progress__bar {
    display: block;
    width: 0;
    height: inherit;
    background-color: #00bfa5;
}


