.newsletter-form {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
}

.newsletter-form__left-col,
.newsletter-form__right-col {
    -ms-flex-preferred-size: 50%;
        flex-basis: 50%;
    -ms-flex-negative: 1;
        flex-shrink: 1;
    -ms-flex-positive: 1;
        flex-grow: 1;
}

.newsletter-form__text {
    font-family: ralewaymedium, Arial, sans-serif;
    font-size: 16px;
    color: #fff;
}

.newsletter-form__form {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    height: 45px;
}

.newsletter-form__input {
    -ms-flex-positive: 1;
        flex-grow: 1;
    height: 100%;
    padding-left: 20px;
    padding-right: 20px;
    border: 1px solid #fff;
    font-family: ralewaybold, Arial, sans-serif;
    font-size: 14px;
    color: #fff;
    background-color: #00bfa5;
    outline-color: #fff;
}

.newsletter-form__input::-webkit-input-placeholder {
    color: #fff;
    opacity: 0.6;
}

.newsletter-form__input:-ms-input-placeholder {
    color: #fff;
    opacity: 0.6;
}

.newsletter-form__input::-ms-input-placeholder {
    color: #fff;
    opacity: 0.6;
}

.newsletter-form__input::placeholder {
    color: #fff;
    opacity: 0.6;
}

.newsletter-form__button {
    height: 100%;
    padding-left: 25px;
    padding-right: 25px;
    margin-left: 10px;
    font-family: ralewaymedium, Arial, sans-serif;
    font-size: 14px;
    text-transform: uppercase;
    border: none;
    color: #00bfa5;
    background-color: #fff;
    cursor: pointer;
}



