.live-class__video-wrapper {
    position: relative;
    height: 0;
    padding-bottom: 56%;
    overflow: hidden;
}

.live-class__video-wrapper iframe {
    position: absolute;
    left: 0;
    top: 0;
    height: 100%;
    width: 100%;
}

.live-class__slider {
    position: relative;
    height: 100%;
}

.live-class__video-wrapper {
    background-color: #000;
    -webkit-box-shadow: 0px 1px 3px 0px rgba(0, 0, 0, 0.35);
            box-shadow: 0px 1px 3px 0px rgba(0, 0, 0, 0.35);
}

.live-class__slider-nav {
    position: relative;
    margin-top: 25px;
    text-align: right;
}

.live-class__slider-counter {
    position: absolute;
    left: 50%;
    -webkit-transform: translateX(-50%);
        -ms-transform: translateX(-50%);
            transform: translateX(-50%);
    font-family: ralewaybold, Arial, sans-serif;
    font-size: 24px;
}

.live-class__slider-nav-arrow {
    margin-left: 2px;
    width: 25px;
    height: 25px;
    border: none;
    outline: none;
    background-repeat: no-repeat;
    background-position: center;
    cursor: pointer;
    -webkit-transition: 0.2s;
    -o-transition: 0.2s;
    transition: 0.2s;
}

.live-class__slider-nav-arrow:hover {
    -webkit-transform: translateY(-1px);
        -ms-transform: translateY(-1px);
            transform: translateY(-1px);
    -webkit-box-shadow: 0px 2px 3px 1px rgba(0,0,0,0.2);
            box-shadow: 0px 2px 3px 1px rgba(0,0,0,0.2);
}

.live-class__slider-nav-arrow_left {
    background-image: url(img/left-arrow.svg);
}

.live-class__slider-nav-arrow_right {
    background-image: url(img/right-arrow.svg);
}
  


